.breadcrumbs {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

.breadcrumbs-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}

.breadcrumb-item {
  display: flex;
  align-items: center;
  line-height: 1.5;
  padding: 4px 0;
}

.separator {
  margin: 0 8px;
  color: #7f7f7f;
}

.breadcrumb-link,
.breadcrumb-text {
  color: #1a73e8;
  text-decoration: none;
  font-weight: 500;
  white-space: nowrap;
  line-height: 1.5;
  padding: 4px 0;
  display: inline-block;
}

.breadcrumb-link:hover {
  text-decoration: underline;
}

.breadcrumb-text {
  color: #202124;
  font-weight: normal;
  cursor: default;
}
